.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.card {
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s ease-in-out;
  border-radius: 0px; /* Rounded corners for the card */
  transform-style: preserve-3d;
  transition: transform 0.3s, box-shadow 0.3s;
  perspective: 1500px; /* Perspective for 3D effect */
}

.card:hover {
  transform: translateY(-10px) rotateX(5deg) rotateY(5deg); /* Rotate and lift the card on hover */
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2); /* Increase box-shadow for depth */
}
.card::before {
  content: '';
  position: absolute;
  top: -10%;
  left: -10%;
  width: 120%;
  height: 120%;
  background: linear-gradient(45deg, rgba(255, 255, 255, 0) 0%, rgba(7, 0, 0, 0) 100%);
  transform: rotateZ(-15deg); /* Adjust gradient angle for reflection */
  opacity: 0.8; /* Adjust opacity for reflection effect */
  pointer-events: none; /* Prevent interaction with reflection layer */
}

@keyframes glow {
  0% {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  }
  50% {
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
  }
  100% {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  }
}
.navbar {
  background: linear-gradient(to left, #45523e, #45523e); /* Red gradient background */
  /* You can adjust the angle and colors as needed */
}

.navbar-brand,
.navbar-nav .nav-link {
  color: #fff; /* Text color for links and brand */
}

.navbar-toggler {
  border-color: #fff; /* Color for the toggler icon */
}

.custom-card {
  border: 2px solid rgba(0, 0, 0, 1); /* Adjust opacity (last value) for desired fade effect */
}
.navbar{
  font-family: "DM Serif Display", serif;
  font-weight: 400;
  font-style: normal;
}
.btn-3d {
  background-color: #ad5d50;
  color: white;
  border: none;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 0px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.2), 0 6px 20px rgba(0,0,0,0.19);
  transition: all 0.3s ease;
}

.btn-3d:hover {
  background-color: #2980b9;
}






